	module.exports = {"data":{"markdownRemark":{"html":"<h2>“<a href=\"/filez/music/will.mp3\">will-o'-the-wisp” を聞く</a> (2:36 | [filesize]<a href=\"https://danmaq.com/filez/music/will.mp3%5B/filesize%5D\">https://danmaq.com/filez/music/will.mp3[/filesize]</a>)</h2>\n<p>幻想的な入りからフィルターをひねったようなブレイクビーツ、そしてトランスを意識したシンセへと展開する曲。途中で入る細切れのリバースシンバルとかパッド音辺りが当時 beatmaniaIIDX の某曲を意識していた証。 今まで作った曲の中で一番展開に悩んだ曲。またジャンル付けにも悩んだ曲(ドラムパターンを入れたのは結構後になってからだったりする)。そしてタイトル付けにも悩んだ曲。確か音割れ(コンプレッサーとか知らなかったし)にも悩んでたなぁ……とにかくいろいろ悩んでいた曲。 でも製作者の悩みに対して曲は悩みを知らずふわふわと浮いては軽快に駆け抜ける感じでよかったかもと思います。 </p>\n<h2>2009/9/7追記</h2>\n<p>BMS版はNORMAL譜面を追加し、従来譜面の難易度設定を7段階から12段階へ引きなおしました。旧難易度は7です。(従来譜面は殆ど弄っていません)</p>\n<ul>\n<li>d'n bass | 2:36 | 84-168BpM | BMSLEVEL:N7-H9 | BMSNOTES:N989-H1190</li>\n<li><a href=\"/filez/music/will.zip\">BMS “will-o'-the-wisp” をダウンロード</a> ([filesize]<a href=\"https://danmaq.com/filez/music/will.zip%5B/filesize%5D\">https://danmaq.com/filez/music/will.zip[/filesize]</a>)</li>\n</ul>","frontmatter":{"cover":null,"date":"2002-05-05T19:31:56+09:00","strDate":"2002/5/5","title":"[BMS] will-o'-the-wisp"}}},"pathContext":{"path":"/ja/blog/2002/05/05-will-o-the-wisp/","langKey":"ja"}}

